<template>
  <label
    v-show="show"
    class="wssearch--checkbox"
    :class="labelClass"
    aria-disabled="false"
  >
    <span
      class="wssearch--checkbox-input"
      aria-disabled="false"
    >
      <input
        :id="createID"
        v-model="modelSelected"
        class="wssearch--checkbox-input-checkbox"
        type="checkbox"
        :value="val"
        aria-disabled="false"
        tabindex="0"
      >
      <span
        class="wssearch--checkbox-input-icon"
        aria-disabled="false"
      />
    </span>
    <span>
      <span class="wssearch--checkbox-title">
        {{ title }}
      </span>
      <span
        class="wssearch--checkbox-count"
        title="count"
      >
        {{ agg.doc_count }}
      </span>
    </span>
  </label>
</template>

<script>
import Vue from 'vue';

export default {
  name: 'FacetCheckbox',
  props: {
    agg: {
      type: Object,
      default() {
        return {};
      },
    },
    name: {
      type: String,
      default: '',
    },
    index: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    modelSelected: {
      set(val) {
        this.$store.commit('SET_SELECTED', val);
      },
      get() {
        return this.$store.state.selected;
      },
    },
    labelClass() {
      let selectedClass = '';
      const { selected } = this.$store.state;
      let keyValue = this.agg.key_as_string ? this.agg.key_as_string : this.agg.key;

      const matches = selected.filter(el => el.value === this.agg.key
      || (this.agg.key_as_string
      && el.value === this.agg.key_as_string));

      if (matches.length) {
        selectedClass = ' wssearch--checkbox-selected';
      }
      if (!keyValue) {
        keyValue = '_';
      }
      return (
        `wssearch--checkbox__${this.strip(this.name)}--${this.strip(keyValue)}${selectedClass}`
      );
    },
    title() {
      let title = '';
      if (this.agg.name) {
        title = this.agg.name;
      } else {
        title = this.agg.key_as_string
          // eslint-disable-next-line no-undef
          ? mw.message(`wssearchfront-${this.agg.key_as_string}`).text()
          : this.agg.key;
      }
      return title;
    },
    createID() {
      let keyValue = this.agg.key_as_string ? this.agg.key_as_string : this.agg.key;
      if (!keyValue) {
        keyValue = '_';
      }
      return (
        `${this.strip(this.name)}--${this.strip(keyValue)}`
      );
    },
    show() {
      return this.agg.show !== 'no' && this.agg.doc_count > 0;
    },
    val() {
      let out = '';
      const keyValue = this.agg.key_as_string ? this.agg.key_as_string : this.agg.key;
      if (this.agg.to) {
        out = {
          value: this.agg.key,
          key: this.name,
          range: {
            gte: Number(`${this.agg.from}.0000000`),
            lte: Number(`${this.agg.to}.0000000`),
          },
        };
      } else {
        out = { value: keyValue, key: this.name };
        if (this.agg.name) {
          out.name = this.agg.name;
        }
        if (this.agg.key_as_string) {
          // eslint-disable-next-line no-undef
          out.name = mw.message(`wssearchfront-${this.agg.key_as_string}`).text();
        }
        if (this.agg.type) {
          out.type = this.agg.type;
        }
      }
      return out;
    },
  },
  mounted() {
    const { selected } = this.$store.state;
    if (selected.length > 0 && this.agg.key_as_string) {
      selected.forEach((element, i) => {
        if (element.key === this.name) {
          // eslint-disable-next-line no-undef
          selected[i].name = mw
            .message(`wssearchfront-${this.agg.key_as_string}`)
            .text();
          Vue.set(this.$store.state.selected, i, selected[i]);
        }
      });
    }
  },
  methods: {
    strip(string) {
      return string.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
  },
};
</script>

<style>
.wssearch--checkbox {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 0.5em;
  align-items: center;
}

.wssearch--checkbox-count {
  margin-left: 0.5em;
  white-space: nowrap;
}

.wssearch--checkbox-count:after {
  content: ")";
}

.wssearch--checkbox-count:before {
  content: "(";
}

.wssearch--checkbox-input-checkbox:checked + .wssearch--checkbox-input-icon {
  background-color: var(--base-color);
  border-color: var(--base-color);
  background-size: 1em 1em;
}
.wssearch--checkbox-input-checkbox:hover + .wssearch--checkbox-input-icon {
  border-color: var(--base-color-lighter);
}
.wssearch--checkbox-input-checkbox:focus + .wssearch--checkbox-input-icon {
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color);
  outline: 1px solid transparent;
}

.wssearch--checkbox-input-checkbox:checked:hover
  + .wssearch--checkbox-input-icon {
  background-color: var(--base-color-lighter);
  border-color: var(--base-color-lighter);
}

.wssearch--checkbox-input-checkbox:checked:focus
  + .wssearch--checkbox-input-icon {
  background-color: var(--base-color);
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color), inset 0 0 0 2px #fff;
}

.wssearch--checkbox-input-icon {
  cursor: pointer;
  -webkit-transition: background-color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, border-color 100ms, box-shadow 100ms;
  transition: background-color 100ms, border-color 100ms, box-shadow 100ms;
  background-color: #fff;
  background-size: 0 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  width: 1.42857143em;
  height: 1.42857143em;
  border-color: #72777d;
  border-style: solid;
  border-radius: 2px;
  border-width: 1px;
  background-position: center center;
  background-repeat: no-repeat;
}

.wssearch--checkbox-input {
  position: relative;
}

.wssearch--checkbox-input-icon {
  background-image: linear-gradient(transparent, transparent), var(--check-icon);
}
</style>
