<template>
  <div
    aria-disabled="false"
    class="wssearch--filter"
    :class="`wssearch--filter__${strip(name)}`"
  >
    <div class="wssearch--filter-header">
      <label>{{ labelName }}</label>
    </div>
    <div class="wssearch--search-wrapper">
      <input
        v-model="termTyped"
        type="text"
        :placeholder="$i18n('search')"
        class="wssearch--facetsearch-input"
        tabindex="0"
        aria-disabled="false"
        @keyup.enter="search"
      >
      <span class="wssearch--search-icon" />
      <span
        v-if="termTyped"
        class="wssearch--search-clear"
        tabindex="-1"
        aria-label="Clear"
        role="button"
        @click="clearTerm"
      />
    </div>
    <span
      v-if="false"
      class="wssearch--search-button"
      aria-label="Search"
      role="button"
      tabindex="1"
      aria-disabled="false"
      @click="search"
    >
      {{ $i18n("search") }}
    </span>
    <input
      :id="setID"
      type="checkbox"
      :value="val"
      style="display: none"
      @change="clearTerm"
    >
  </div>
</template>

<script>
export default {
  name: 'FacetSearch',
  props: {
    name: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      termClear: '',
      userHasTyped: false,
      termTyped: '',
    };
  },
  computed: {
    labelName() {
      return this.label ? this.label : this.name;
    },
    val() {
      return { key: this.name, value: this.termTyped, type: 'query' };
    },
    setID() {
      return `${this.strip(this.name)}--${this.strip(this.termTyped)}`;
    },
    term: {
      set(value) {
        this.termClear = value;
        this.userHasTyped = true;
      },
      get() {
        if (this.termClear || this.userHasTyped) {
          return this.termClear;
        }
        return this.$store.state.term;
      },
    },
  },
  methods: {
    strip(string) {
      return string.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
    filterSelected() {
      const { selected } = this.$store.state;
      return selected.filter(el => el.key !== this.name);
    },
    search() {
      const newSelected = this.filterSelected();
      if (this.termTyped) {
        newSelected.push({ key: this.name, value: this.termTyped, type: 'query' });
      }
      this.$store.commit('SET_SELECTED', newSelected);
    },
    clearTerm() {
      const newSelected = this.filterSelected();
      this.termTyped = '';
      this.$store.commit('SET_SELECTED', newSelected);
    },
  },
};
</script>

<style>
.wssearch--facetsearch-input {
  box-sizing: border-box;
  cursor: text;
  box-shadow: inset 0 0 0 1px transparent;
  -webkit-transition: border-color 250ms, box-shadow 250ms;
  -moz-transition: border-color 250ms, box-shadow 250ms;
  transition: border-color 250ms, box-shadow 250ms;
  background-color: #fff;
  color: #000;
  margin: 0;
  border: 1px solid #a2a9b1;
  border-radius: 2px;
  padding: 0.4em 0.6em 0.4em 2.2em;
  font-size: inherit;
  font-family: inherit;
  line-height: 1.42857143em;
  width: 100%;
}

.wssearch--facetsearch-input:hover {
  border-color: #72777d;
}
.wssearch--facetsearch-input:focus {
  outline: 0;
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color);
}
</style>
