<template>
  <div class="wssearch--order">
    <span class="wssearch--order__label">
      {{ $i18n("wssearchfront-order") }}
    </span>
    <div class="wssearch--order__wrapper">
      <span
        class="wssearch--order__select"
        tabindex="0"
        aria-disabled="false"
      >
        {{
          sortOrderType == "score"
            ? $i18n("wssearchfront-score")
            : sortOrderType
        }}
      </span>
      <div class="wssearch--order__options">
        <span
          class="wssearch--order__option"
          @click="$store.commit('SET_ORDERTYPE', 'score')"
        >
          {{ $i18n("wssearchfront-score") }}
        </span>
        <span
          v-for="(order, key) in settings['sort options']"
          :key="key"
          class="wssearch--order__option"
          @click="$store.commit('SET_ORDERTYPE', key)"
        >
          {{ order.label || key }}
        </span>
      </div>
    </div>
    <span
      class="wssearch--order__sortorder"
      @click="setOrder"
    >
      <span :class="`wssearch--order__icon wssearch--order__${sortOrder}`" />
    </span>
  </div>
</template>

<script>
export default {
  name: 'SortOrder',
  props: {
    settings: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    sortOrderType() {
      const option = this.$store.state.sortOrderType;
      const type = this.settings['sort options'][option];
      return type && type.label ? type.label : option;
    },
    sortOrder() {
      return this.$store.state.sortOrder;
    },
  },
  methods: {
    setOrder() {
      return this.sortOrder === 'desc'
        ? this.$store.commit('SET_ORDER', 'asc')
        : this.$store.commit('SET_ORDER', 'desc');
    },
  },
};
</script>

