<template>
  <div class="wssearch--pagers">
    <div
      v-if="settings['size options']"
      class="wssearch--size"
    >
      <div class="wssearch--size__wrapper">
        <span
          class="wssearch--size__select"
          tabindex="0"
          aria-disabled="false"
        >
          {{
            size == 999
              ? $i18n("wssearchfront-all")
              : size + $i18n("wssearchfront-page")
          }}
        </span>
        <div class="wssearch--size__options">
          <span
            v-for="(option, key) in settings['size options']"
            :key="key"
            class="wssearch--size__option"
            @click="$store.commit('SET_SIZE', option)"
          >
            {{ option + $i18n("wssearchfront-page") }}
          </span>
          <span
            class="wssearch--size__option"
            @click="$store.commit('SET_SIZE', 999)"
          >
            {{ $i18n("wssearchfront-all") }}
          </span>
        </div>
      </div>
    </div>
    <div v-if="total > size">
      <span
        class="wssearch--pager wssearch--pager-back"
        :class="showBack"
        @click="back"
      >
        <span class="wssearch--pager-back-icon" />
      </span>
      <span
        class="wssearch--pager"
        :class="activePage(1)"
        @click="move"
      >
        <span> 1 </span>
      </span>
      <span
        v-show="from / size > 2 && total / size > 5"
        class="wssearch--pager wssearch--dots"
        :class="showBack"
      >
        ...
      </span>
      <span
        v-for="(pager, index) in pagers"
        :key="index"
        class="wssearch--pager"
        :class="activePage(pager)"
        @click="move"
      >
        <span>
          {{ pager }}
        </span>
      </span>
      <span
        v-show="from / size < last - 3 && total / size > 5"
        class="wssearch--pager wssearch--dots"
      >
        ...
      </span>
      <span
        class="wssearch--pager"
        :class="activePage(last)"
        @click="move"
      >
        <span>
          {{ last }}
        </span>
      </span>

      <span
        class="wssearch--pager wssearch--pager-forward"
        :class="showForward"
        @click="next"
      >
        <span class="wssearch--pager-forward-icon" />
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HitPagers',
  props: {
    total: {
      type: Number,
      default: 0,
    },
    from: {
      type: Number,
      default: 0,
    },
    size: {
      type: Number,
      default: 0,
    },
    settings: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    last() {
      return Math.ceil(this.total / this.size);
    },
    showBack() {
      return this.from < 1 ? 'wssearch--pager-hide' : '';
    },
    showForward() {
      return this.from + this.size >= this.total ? 'wssearch--pager-hide' : '';
    },
    pagers() {
      if (this.total >= this.size) {
        const pages = [];
        let i;
        const step = Math.ceil(this.total / this.size);

        if (step > 5) {
          if (this.from / this.size < 3) {
            for (i = 1; i < 4; i += 1) {
              pages.push(i + 1);
            }
          } else if (this.from / this.size > this.last - 4) {
            for (i = this.last - 4; i < this.last; i += 1) {
              pages.push(i);
            }
          } else {
            pages.push(this.from / this.size);
            pages.push(this.from / this.size + 1);
            pages.push(this.from / this.size + 2);
          }
          return pages;
        }
        for (i = 0; i < step; i += 1) {
          if (i + 1 > 1 && i + 1 < this.last) {
            pages.push(i + 1);
          }
        }
        return pages;
      }
      return false;
    },
  },
  methods: {
    activePage(pager) {
      if (pager === this.from / this.size + 1) {
        return 'active';
      }
      return false;
    },
    back() {
      const from = this.from - this.size;
      this.$store.commit('SET_FROM', from);
    },
    move(e) {
      const from = Math.ceil(this.size * (e.target.innerText - 1));
      this.$store.commit('SET_FROM', from);
    },
    next() {
      const from = this.from + this.size;
      this.$store.commit('SET_FROM', from);
    },
  },
};
</script>

<style>
.wssearch--pagers {
  display: flex;
  align-items: center;
}
.wssearch--pagers {
  justify-self: end;
  margin-top: 1.5em;
}
.wssearch--size__wrapper {
  width: 100%;
  position: relative;
  min-width: 12ch;
}
.wssearch--pagers .wssearch--pager {
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  cursor: pointer;
  width: 2em;
  display: inline-flex;
  justify-content: center;
  border-radius: 2px;
}
.wssearch--pagers .wssearch--pager:hover {
  background-color: var(--base-color-lighter);
  color: white;
}
.wssearch--pagers .wssearch--pager.active {
  font-weight: bold;
  color: white;
  background-color: var(--base-color);
}
.wssearch--pagers .wssearch--pager-back {
  color: transparent;
}
.wssearch--pagers .wssearch--pager-back-icon,
.wssearch--pagers .wssearch--pager-forward-icon {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  width: 60%;
}
.wssearch--pagers .wssearch--pager-back-icon {
  background-image: var(--back-icon);
}
.wssearch--pagers .wssearch--pager-forward-icon {
  background-image: var(--next-icon);
}
.wssearch--pagers .wssearch--pager-back:hover,
.wssearch--pagers .wssearch--pager-forward:hover {
  color: transparent;
  background-color: var(--tint-1);
}
.wssearch--pager-back-icon:after,
.wssearch--pager-forward-icon:after {
  content: "z";
  color: transparent;
}
.wssearch--pagers .wssearch--pager-hide,
.wssearch--pagers .wssearch--pager-hide .wssearch--pager-forward-icon,
.wssearch--pagers .wssearch--pager-hide .wssearch--pager-back-icon {
  background-image: none;
  pointer-events: none;
  color: white;
}
.wssearch--pagers .wssearch--dots {
  pointer-events: none;
}
</style>
