<template>
  <div id="app">
    <div
      class="wssearch"
      :class="selectedClass"
    >
      <search-input />
      <selected-pils />
      <sort-order
        v-if="settings['sort options']"
        :settings="settings"
      />
      <div
        v-if="state.aggs"
        class="wssearch--filters"
      >
        <component
          :is="filterObject.component"
          v-for="(filterObject, name) in filters"
          :key="name"
          :translation="facetSettings[name].translation"
          :query="facetSettings[name].query"
          :queryText="facetSettings[name].text"
          :queryData="facetSettings[name].data"
          :type="facetSettings[name].type"
          :buckets="
            state.aggs[name] && state.aggs[name][name] ? state.aggs[name][name].buckets : []
          "
          :label="facetSettings[name].label"
          :name="name"
        />
      </div>
      <div class="wssearch--total">
        <b
          v-if="state.total > 0"
          class="wssearch--total__nr"
        >
          {{ state.total }}
        </b>
        {{ $i18n('wssearchfront-total', state.total) }}
      </div>
      <div
        class="wssearch--main"
        :class="[layoutClass, loadingClass]"
      >
        <div
          class="wssearch--hits"
          :style="`--table-columns:${tableHeaders.length}`"
        >
          <div
            v-if="layout"
            class="wssearch--table__header"
          >
            <span
              v-for="(hitID, key) in tableHeaders"
              :key="key"
            >
              {{ hitID.name }}
            </span>
          </div>
          <hit-item
            v-for="(hit, i) in state.hits"
            :key="i"
            :hit="hit"
          />
        </div>
        <hit-pagers
          :size="state.size"
          :from="state.from"
          :total="state.total"
          :settings="settings"
        />
      </div>
    </div>
  </div>
</template>

<script>
import store from './store';
import SearchInput from './components/SearchInput.vue';
import PillsSelected from './components/PillsSelected.vue';
import SortOrder from './components/SortOrder.vue';
import HitItem from './components/HitItem.vue';
import HitPagers from './components/HitPagers.vue';
import FacetDateRange from './components/FacetDateRange.vue';
import FacetRangeSlider from './components/FacetRangeSlider.vue';
import FacetCombobox from './components/FacetCombobox.vue';
import FacetAskCombobox from './components/FacetAskCombobox.vue';
import FacetFilter from './components/FacetFilter.vue';
import FacetSearch from './components/FacetSearch.vue';
import FacetSorted from './components/FacetSorted.vue';

export default {
  store,
  name: 'App',
  components: {
    'search-input': SearchInput,
    'selected-pils': PillsSelected,
    'hit-item': HitItem,
    'hit-pagers': HitPagers,
    'sort-order': SortOrder,
  },
  data() {
    return {
      FacetCombobox,
      FacetFilter,
      FacetAskCombobox,
      FacetSearch,
      FacetSorted,
      FacetDateRange,
      FacetRangeSlider,
      // eslint-disable-next-line no-undef
      facetSettings: mw.config.values.WSSearchFront.config.facetSettings,
      // eslint-disable-next-line no-undef
      hitSettings: mw.config.values.WSSearchFront.config.hitSettings,
      // eslint-disable-next-line no-undef
      settings: mw.config.values.WSSearchFront.config.settings,
    };
  },
  computed: {
    state() {
      return this.$store.state;
    },
    selectedClass() {
      return this.state.selected.map(el => `wss-selected--${this.strip(el.key)}--${this.strip(el.value)}`)
        .join(' ');
    },
    layoutClass() {
      return this.layout ? 'wssearch--table' : '';
    },
    loadingClass() {
      return this.state.loading ? 'wssearch--main__loading' : '';
    },
    tableHeaders() {
      const pagelabel = this.settings.title && this.settings.title.label ? this.settings.title.label : 'Page';
      const headers = [{ name: pagelabel }, { name: 'Content' }];

      Object.entries(this.hitSettings).forEach(entrie => {
        if (entrie[1].label) {
          headers.push({ name: entrie[1].label });
        } else {
          headers.push({ name: entrie[0] });
        }
      });

      headers.push({ name: 'Page title' }, { name: 'Modification date' });

      return headers;
    },
    layout() {
      return this.settings.layout === 'table';
    },
    filters() {
      const out = {};
      const components = {
        combobox: this.FacetCombobox,
        sorted: this.FacetSorted,
        search: this.FacetSearch,
        datepicker: this.FacetDateRange,
        'ask combobox': this.FacetAskCombobox,
        slider: this.FacetRangeSlider,
      };

      Object.keys(this.facetSettings).forEach(key => {
        if (this.state.aggs[key] && this.state.aggs[key][key]) {
          out[key] = this.state.aggs[key][key];
        } else {
          out[key] = {};
        }

        if (this.facetSettings[key].display) {
          out[key].component = components[this.facetSettings[key].display] || this.FacetFilter;
        } else {
          out[key].component = this.FacetFilter;
        }
      });
      return out;
    },
  },
  mounted() {
    this.$store.commit('START', true);
  },
  methods: {
    strip(string) {
      return string.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
  },
};
</script>

