<template>
  <div
    v-if="buckets.length"
    :class="
      `wssearch--filter wssearch--filter__${name.toLowerCase().replace(/[^a-z0-9]/gim, '_')}`
    "
  >
    <span class="wssearch--filter-header">
      <label>
        {{ filterName }}
      </label>
    </span>
    <div class="wssearch--selected-filters__combobox">
      <pill-item
        v-for="(activefilter, index) in pils"
        :key="index"
        :activefilter="activefilter"
        :label="activefilter.name"
      />
    </div>
    <div class="wssearch--filter-options-search-wrapper">
      <span
        v-if="term"
        class="wssearch--search-clear wssearch--search-clear__combobox"
        tabindex="-1"
        aria-label="Clear"
        role="button"
        @click="clearInput"
      />
      <input
        type="input"
        :value="term"
        :placeholder="filterName"
        class="wssearch--filter-options-search"
        @input="searchAggs"
        @keyup.enter="searchForTerm"
      >
      <div class="wssearch--filter-options wssearch--filter-options__combobox">
        <facet-checkbox
          v-for="(agg, i) in sortedBuckets"
          :key="i + agg.key + name"
          :agg="agg"
          :index="i"
          :name="name"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import PillItem from './PillItem.vue';
import FacetCheckbox from './FacetCheckbox.vue';

export default {
  name: 'FacetComboBox',
  components: {
    'pill-item': PillItem,
    'facet-checkbox': FacetCheckbox,
  },
  props: {
    name: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    buckets: {
      type: [Array, Object],
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      term: '',
      // eslint-disable-next-line no-undef
      facetSettings: mw.config.values.WSSearchFront.config.facetSettings,
    };
  },
  computed: {
    filterName() {
      return this.label || this.name.replace('_', ' ');
    },
    sortedBuckets() {
      const unsorted = this.buckets;
      let sorted = '';
      if (
        this.facetSettings[this.name]
        && this.facetSettings[this.name].sort
        === 'alphabetically'
      ) {
        sorted = unsorted.sort((a, b) => {
          const textA = a.key.toUpperCase();
          const textB = b.key.toUpperCase();
          // eslint-disable-next-line no-nested-ternary
          return textA < textB ? -1 : textA > textB ? 1 : 0;
        });
      } else {
        sorted = this.buckets;
      }
      return sorted;
    },
    pils() {
      const { selected } = this.$store.state;
      return selected.filter((el) => el.key === this.name);
    },
  },
  methods: {
    searchForTerm(e) {
      this.term = e.target.value;
      this.$emit('search', this.term);
    },
    searchAggs(e) {
      this.term = e.target.value;
      this.$emit('input', this.term);
      const regex = new RegExp(this.term, 'ig');
      this.buckets.forEach((agg) => {
        if (!agg.key.match(regex)) {
          Vue.set(agg, 'show', 'no');
        } else {
          Vue.set(agg, 'show', 'yes');
        }
      });
    },
    clearInput() {
      this.term = '';
    },
  },
};
</script>

<style >
