<template>
  <div
    :class="
      `wssearch--filter
       wssearch--slider
       wssearch--slider__${strip(name)}
       wssearch--slider__${strip(name)}--${realVal}`
    "
    :style="`--slider-val:${realVal};--slider-val2:${realVal2}`"
  >
    <span class="wssearch--filter-header">
      <label>
        {{ name.replace(/_/g, ' ') }}
      </label>
    </span>
    <transition name="slide">
      <div
        v-show="isSelected"
        class="wssearch--filter-values"
      >
        <span class="wssearch--filter-value1">{{ slideVal }}</span>
        <span class="wssearch--filter-value2">{{ slideVal2 }}</span>
      </div>
    </transition>
    <div
      class="wssearch--slider-input"
      :class="sliderActiveClass"
    >
      <input
        v-model="modalVal"
        type="range"
        :max="max"
      >
      <input
        v-model="modalVal2"
        type="range"
        :max="max"
      >
      <span class="wssearch--slider-input-icon" />
    </div>
    <facet-checbox
      :name="name"
      :agg="agg"
      :index="0"
    />
  </div>
</template>

<script>
import FacetCheckbox from './FacetCheckbox.vue';

export default {
  name: 'FacetRangeSlider',
  components: {
    'facet-checbox': FacetCheckbox,
  },
  props: {
    name: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      slideVal: 0,
      range: 5,
      slideVal2: 3,
      // eslint-disable-next-line no-undef
      facetSettings: mw.config.values.WSSearchFront.config.facetSettings,
    };
  },
  computed: {
    sliderActiveClass() {
      return this.isSelected ? '' : 'wssearch--slider__inactive';
    },
    isSelected() {
      const { selected } = this.$store.state;
      return selected.filter((el) => el.value === this.name).length;
    },
    max() {
      return this.facetSettings[this.name].max;
    },
    modalVal2: {
      set(val) {
        this.slideVal2 = val;
        const { selected } = this.$store.state;
        Object.keys(selected).forEach((_, i) => {
          if (selected && selected[i] && selected[i].value === this.name) {
            selected[i].range = { gte: this.slideVal, lte: this.slideVal2 };

            this.$store.commit('SET_SELECTED', selected);
          }
        });
      },
      get() {
        return this.slideVal2;
      },
    },
    modalVal: {
      set(val) {
        this.slideVal = val;
        const { selected } = this.$store.state;
        Object.keys(selected).forEach((_, i) => {
          if (selected && selected[i] && selected[i].value === this.name) {
            selected[i].range = { gte: this.slideVal, lte: this.slideVal2 };

            this.$store.commit('SET_SELECTED', selected);
          }
        });
      },
      get() {
        return this.slideVal;
      },
    },
    realVal() {
      return (100 / this.max) * this.slideVal;
    },
    realVal2() {
      return (100 / this.max) * this.slideVal2;
    },
    agg() {
      return {
        key: this.name, from: this.slideVal, to: this.slideVal2, doc_count: 1,
      };
    },
  },
  methods: {
    strip(string) {
      return string.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
  },
};
</script>

<style >
.wssearch--filter-values{
  text-align: center;
}

.wssearch--slider-input{
  position: relative;
}
.wssearch--slider-input-icon {
    height: 1em;
    background: linear-gradient(
    90deg,
     #eaecf0 calc(var(--slider-val) * 1%),
     var(--base-color) calc(var(--slider-val) * 1%),
     var(--base-color) calc(var(--slider-val2) * 1%),
    #eaecf0 calc(var(--slider-val2) * 1%)
    );
     width: calc(100% - 1.5em);
    top: 1px;
    pointer-events: none;
    position: absolute;
    overflow: hidden;
    left: 1.5em;
    border: 2px solid #a2a9b1;
    border-radius: 13px;
}
.wssearch--slider-input input {
    width: calc(100% - 1.5em);
    top: 0;
    pointer-events: none;
    position: absolute;
    left: 1.5em;
}

.wssearch--slider-input input::-webkit-slider-thumb {
    pointer-events: all;
    position: relative;
    z-index: 1;
    outline: 0;
    transform: scale(1.2);
}

.wssearch--slider-input input::-moz-range-thumb {
    pointer-events: all;
    position: relative;
    z-index: 10;
    -moz-appearance: none;
    width: 9px;
     transform: scale(1.2);
}
.wssearch--slider__inactive {
  filter: grayscale(1);
}
.wssearch--slider .wssearch--checkbox-title,
.wssearch--slider .wssearch--checkbox-count{
  display: none;
}
.wssearch--filter-values{
  position: relative;
  height: 1.5em;
  margin-left: 1.5em;
  width: calc(100% - 1.5em);
}
.wssearch--filter-value1{
  position: absolute;
  left: calc(var(--slider-val) * 1%);
}
.wssearch--filter-value2{
  position: absolute;
  left: calc(var(--slider-val2) * 1%);
}

.slide-enter-active {
   transition-duration: 0.3s;
   transition-timing-function: ease-in;
}

.slide-leave-active {
   transition-duration: 0.3s;
   transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.slide-enter-to, .slide-leave {
   max-height: 100px;
   overflow: hidden;
}

.slide-enter, .slide-leave-to {
   overflow: hidden;
   max-height: 0;
}
</style>
