<template>
  <div class="wssearch--selected">
    <pill-item
      v-for="(activefilter, index) in selected"
      :key="index"
      :activefilter="activefilter"
      :label="activefilter.name"
    />
    <span
      v-if="selected.length"
      class="wssearch--selected-clear"
      tabindex="-1"
      aria-label="Clear"
      role="button"
      @click="clearFilters"
    >
      {{ $i18n("wssearchfront-clear-all-filters") }}
    </span>
    <div style="display: none">
      <facet-checkbox
        v-for="(agg, i) in hiddenBoxes"
        :key="i"
        :agg="buildAggs(agg)"
        :index="i"
        :name="agg.key"
      />
    </div>
  </div>
</template>

<script>
import PillItem from './PillItem.vue';
import FacetCheckbox from './FacetCheckbox.vue';

export default {
  name: 'PillsSelected',
  components: {
    'pill-item': PillItem,
    'facet-checkbox': FacetCheckbox,
  },
  computed: {
    selected() {
      return this.$store.state.selected;
    },
    hiddenBoxes() {
      return this.selected.filter((sel) => !this.$store.state.aggs
          || !this.$store.state.aggs[sel.key]
          || this.$store.state.aggs[sel.key][sel.key].buckets.length === 0
          || this.$store.state.aggs[sel.key].doc_count === 0
          || this.$store.state.aggs[sel.key][sel.key].doc_count === 0);
    },
  },
  methods: {
    buildAggs(agg) {
      const out = { doc_count: 1, key: agg.value };
      if (agg.name) {
        out.name = agg.name;
      }

      if (agg.range) {
        out.from = agg.range.gte;
        out.to = agg.range.lte;
      }

      return out;
    },
    clearFilters() {
      // eslint-disable-next-line no-undef
      if (mw.config.values.WSSearchFront.config.settings.clear) {
        this.$store.commit('SET_TERM', '');
      }
      this.$store.commit('SET_SELECTED', []);
    },
  },
};
</script>

<style>
.wssearch--selected {
  grid-area: selected;
}
.wssearch--selected-clear {
  white-space: nowrap;
  cursor: pointer;
  color: var(--base-color);
  border: 1px solid transparent;
  padding: 6px 6px;
}
</style>
