<template>
  <div
    class="wssearch--search"
    aria-disabled="false"
  >
    <div class="wssearch--search-wrapper">
      <input
        v-model="term"
        type="text"
        class="wssearch--search-input"
        tabindex="0"
        aria-disabled="false"
        :placeholder="$i18n('search')"
        @keyup.enter="search"
      >
      <span class="wssearch--search-icon" />
      <span
        v-if="term"
        class="wssearch--search-clear"
        tabindex="-1"
        aria-label="Clear"
        role="button"
        @click="clearTerm"
      />
    </div>
    <span
      class="wssearch--search-button"
      aria-label="Search"
      role="button"
      tabindex="1"
      aria-disabled="false"
      @click="search"
    >
      {{ $i18n("search") }}
    </span>
  </div>
</template>

<script>
export default {
  name: 'SearchInput',
  data() {
    return {
      typedTerm: '',
      userHasTyped: false,
    };
  },
  computed: {
    term: {
      set(value) {
        this.typedTerm = value.replace(/[']/gim, '"');
        this.userHasTyped = true;
      },
      get() {
        return this.typedTerm || this.userHasTyped
          ? this.typedTerm
          : this.$store.state.term;
      },
    },
  },
  methods: {
    search() {
      this.$store.commit('SET_TERM', this.typedTerm);
    },
    clearTerm() {
      this.typedTerm = '';
      this.$store.commit('SET_TERM', '');
    },
  },
};
</script>

