<template>
  <div>
    <div
      v-for="(typer, i) in outputBuckets"
      :key="i"
      :class="'wssearch--filter'"
    >
      <span
        v-if="typer.buckets.length"
        class="wssearch--filter-header"
      >
        <label>
          {{ i }}
        </label>
      </span>
      <div class="wssearch--filter-options">
        <template
          v-if="typer.buckets.length > collapsed && !open.includes(i)"
        >
          <facet-checbox
            v-for="(agg, j) in typer.buckets.slice(0, collapsed)"
            :key="j + agg.key + name"
            :agg="agg"
            :index="j"
            :name="name"
          />
          <facet-checbox
            v-for="(agg, k) in typer.buckets.slice(
              collapsed,
              typer.buckets.length
            )"
            :key="k + agg.key + name"
            :agg="agg"
            :index="k"
            :name="name"
            style="display: none"
          />
        </template>
        <template v-else>
          <facet-checbox
            v-for="(agg, l) in typer.buckets"
            :key="l + agg.key + name"
            :agg="agg"
            :index="l"
            :name="name"
          />
        </template>
        <label
          v-if="typer.buckets.length > collapsed"
          class="wssearch--filter-showmore"
          @click="lessOrMore(i)"
        >
          <span>
            {{ $i18n(open.includes(i) ? "wssearchfront-less" : "wssearchfront-more") }}
          </span>
          <span
            class="wssearch--filter-showmore-icon"
            :class="
              open.includes(i)
                ? 'wssearch--filter-icon__less'
                : 'wssearch--filter-icon__more'
            "
          />
        </label>
      </div>
    </div>
  </div>
</template>

<script>
import FacetCheckbox from './FacetCheckbox.vue';

export default {
  name: 'FacetSorted',
  components: {
    'facet-checbox': FacetCheckbox,
  },
  props: {
    name: {
      type: String,
      default: '',
    },
    buckets: {
      type: [Array, Object],
      default() {
        return {};
      },
    },
    queryData: {
      type: String,
      default: '',
    },
    query: {
      type: String,
      default: '',
    },
    queryText: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      open: [],
      // eslint-disable-next-line no-undef
      facetSize: mw.config.values.WSSearchFront.config.settings.facets,
      translations: '',
    };
  },
  computed: {
    collapsed() {
      return this.facetSize ? parseInt(this.facetSize, 10) : 5;
    },
    outputBuckets() {
      const out = {};
      const newTranslations = this.translations;
      this.buckets.forEach((element) => {
        if (newTranslations[element.key]) {
          if (!out[newTranslations[element.key]]) {
            out[newTranslations[element.key]] = { buckets: [element] };
          } else {
            out[newTranslations[element.key]].buckets.push(element);
          }
        }
      });

      const sorted = Object.keys(out)
        .sort()
        .reduce(
          (acc, key) => ({
            ...acc,
            [key]: out[key],
          }),
          {},
        );

      return sorted;
    },
  },
  mounted() {
    const params = {
      action: 'ask',
      query: `${this.query}|limit=500|?${this.queryData}|?${this.queryText}`,
      format: 'json',
      formatversion: 2,
    };
    this.$store.dispatch('doApiCall', {
      actions:
        {
          params,
          component: this,
        },
    });
  },
  methods: {
    apiResult(data) {
      const stripQueryData = this.queryData.split('.');
      const strip = stripQueryData[stripQueryData.length - 1];
      const newTranslations = {};
      let sr = '';
      let rs = '';
      if (data.query) {
        Object.entries(data.query.results).forEach((element) => {
          const queryResult = element[1];
          if (
            queryResult.printouts
            && queryResult.printouts[this.queryText]
            && queryResult.printouts[strip]
          ) {
            if (
              queryResult.printouts[this.queryText][0]
              && queryResult.printouts[this.queryText][0].fulltext
            ) {
              sr = queryResult.printouts[this.queryText][0].fulltext;
            } else {
              [sr] = queryResult.printouts[this.queryText];
            }

            if (queryResult.printouts[strip][0] && queryResult.printouts[strip][0].fulltext) {
              rs = queryResult.printouts[strip][0].fulltext;
            } else {
              [rs] = queryResult.printouts[strip];
            }
            newTranslations[sr] = rs;
          }
          this.translations = newTranslations;
        });
      }
    },
    lessOrMore(ob) {
      const index = this.open.indexOf(ob);
      if (index > -1) {
        this.open.splice(index, 1);
      } else {
        this.open.push(ob);
      }
    },
  },
};
</script>

<style >
.wssearch--filter-showmore {
  display: block;
  width: 100%;
  height: auto;
  cursor: pointer;
  background-color: var(--tint-1);
  border: var(--border-1);
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  display: inline-flex;
  justify-content: center;
  border-radius: 2px;
  font-weight: bold;
}
.wssearch--filter-showmore:hover {
  border-color: var(--base-color);
  background-color: white;
  color: var(--base-color);
}
.wssearch--filter-showmore:focus,
.wssearch--filter-showmore:active {
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color), inset 0 0 0 2px #fff;
  outline: 1px solid transparent;
}

.wssearch--filter-showmore-icon {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  display: inline-block;
  width: 1em;
  margin-left: 1em;
}
.wssearch--filter-icon__more {
  background-image: var(--down-icon);
}
.wssearch--filter-icon__less {
  background-image: var(--up-icon);
}
</style>
