<template>
  <div
    :class="`wssearch--filter wssearch--filter__${strip(name)}`"
  >
    <span
      v-if="strippedBuckets.length"
      class="wssearch--filter-header"
    >
      <label>
        {{ cleanName }}
      </label>
    </span>
    <div class="wssearch--filter-options">
      <facet-checbox
        v-for="(agg, i) in bucketsToShow"
        v-show="i < collapsed || open"
        :key="i + agg.key + name"
        :agg="agg"
        :index="i"
        :name="name"
      />
      <label
        v-if="strippedBuckets.length > collapsed"
        class="wssearch--filter-showmore"
        @click="open = !open"
      >
        <span>
          {{ $i18n(`wssearchfront-${lessOrMore}`) }}
        </span>
        <span
          class="wssearch--filter-showmore-icon"
          :class="`wssearch--filter-icon__${lessOrMore}`"
        />
      </label>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import FacetCheckbox from './FacetCheckbox.vue';

export default {
  name: 'FacetFilter',
  components: {
    'facet-checbox': FacetCheckbox,
  },
  props: {
    name: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    buckets: {
      type: [Array, Object],
      default() {
        return {};
      },
    },
    translation: {
      type: String,
      default: '',
    },
    query: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      open: false,
      // eslint-disable-next-line no-undef
      config: mw.config.values.WSSearchFront.config,
      translations: '',
      strippedBuckets: '',
      bucketsToShow: '',
      fired: false,
    };
  },
  computed: {
    collapsed() {
      return this.config.settings.facets
        ? parseInt(this.config.settings.facets, 10)
        : 5;
    },
    propChange() {
      return this.buckets;
    },
    cleanName() {
      return this.label || this.name.replace(/_/g, ' ');
    },
    lessOrMore() {
      return this.open ? 'less' : 'more';
    },
  },
  watch: {
    propChange() {
      this.organize();
    },
  },
  mounted() {
    if (this.translation) {
      const params = {
        action: 'ask',
        query: `${this.query}|limit=500|?${this.translation}`,
        format: 'json',
        formatversion: 2,
      };
      this.$store.dispatch('doApiCall', {
        actions:
          {
            params,
            component: this,
          },
      });
    } else {
      this.organize();
    }
  },
  methods: {
    apiResult(data) {
      if (data.query) {
        this.translations = data.query.results;
        this.organize();
      }
    },
    organize() {
      let organizedBuckets = [];
      const { selected } = this.$store.state;

      // check if buckets are an array, if not create array from the object
      organizedBuckets = Array.isArray(this.buckets)
        ? this.buckets
        : Object.entries(this.buckets).map((entrie) => {
          const out = entrie[1];
          [out.key] = entrie;
          return out;
        });

      // remove buckets wioth 0 doc count
      organizedBuckets = organizedBuckets
        ? organizedBuckets.filter((el) => el.doc_count > 0)
        : [];

      if (this.translation) {
        organizedBuckets.forEach((element, i) => {
          const transKey = this.translations[element.key];
          if (
            transKey
            && transKey.printouts[this.translation]
          ) {
            if (transKey.printouts[this.translation][0].fulltext) {
              organizedBuckets[i].name = transKey.printouts[this.translation][0].fulltext;
            } else {
              [organizedBuckets[i].name] = transKey.printouts[this.translation];
            }
          }
        });

        if (selected.length > 0 && !this.fired) {
          selected.forEach((element, i) => {
            const transValue = this.translations[element.value];
            if (
              transValue
              && transValue.printouts[this.translation]
            ) {
              if (
                transValue.printouts[this.translation][0].fulltext
              ) {
                selected[i].name = transValue.printouts[this.translation][0].fulltext;
              } else {
                [selected[i].name] = transValue.printouts[this.translation];
              }
              Vue.set(this.$store.state.selected, i, selected[i]);
            }
          });

          this.fired = true;
        } else {
          this.fired = true;
        }
      }

      if (this.type === 'date') {
        this.strippedBuckets = organizedBuckets.reverse();
      } else if (
        this.config.facetSettings[this.name]
        && this.config.facetSettings[this.name].sort
        === 'alphabetically'
      ) {
        this.strippedBuckets = organizedBuckets.sort((a, b) => {
          const textA = a.key.toUpperCase();
          const textB = b.key.toUpperCase();
          // eslint-disable-next-line no-nested-ternary
          return textA < textB ? -1 : textA > textB ? 1 : 0;
        });
      } else {
        this.strippedBuckets = organizedBuckets;
      }

      this.bucketsToShow = this.strippedBuckets;
    },
    strip(string) {
      return string.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
  },
};
</script>

<style >
.wssearch--filter-showmore {
  display: block;
  width: 100%;
  height: auto;
  cursor: pointer;
  background-color: var(--tint-1);
  border: var(--border-1);
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  display: inline-flex;
  justify-content: center;
  border-radius: 2px;
  font-weight: bold;
}
.wssearch--filter-showmore:hover {
  border-color: var(--base-color);
  background-color: white;
  color: var(--base-color);
}
.wssearch--filter-showmore:focus,
.wssearch--filter-showmore:active {
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color), inset 0 0 0 2px #fff;
  outline: 1px solid transparent;
}

.wssearch--filter-showmore-icon {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  display: inline-block;
  width: 1em;
  margin-left: 1em;
}
.wssearch--filter-icon__more {
  background-image: var(--down-icon);
}
.wssearch--filter-icon__less {
  background-image: var(--up-icon);
}
</style>
