<template>
  <span
    :class="propertyClass"
    v-html="outputHtml"
  />
</template>

<script>

export default {
  name: 'HitProp',
  props: {
    keyid: {
      type: Number,
      default: 0,
    },
    type: {
      type: Object,
      default() {
        return {};
      },
    },
    hit: {
      type: Object,
      default() {
        return {};
      },
    },
    name: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      outputHtml:
        '<div class="wssearch-hit-prop-loading"><div class="wssearch-hit-prop-loading-bg"></div></div>',
      // eslint-disable-next-line no-undef
      settings: mw.config.values.WSSearchFront.config.settings,
      monthNames: [
        'jan',
        'feb',
        'mar',
        'apr',
        'may',
        'jun',
        'jul',
        'aug',
        'sep',
        'oct',
        'nov',
        'dec',
      ],
    };
  },
  computed: {
    hitSource() {
      // eslint-disable-next-line no-underscore-dangle
      return this.hit._source;
    },
    propChange() {
      return this.hitSource.subject.title + this.justTheValue;
    },
    justTheValue() {
      let propertyValue;
      const key = `P:${this.keyid}`;
      if (
        this.hitSource[key] && this.hitSource[key][this.type.type]
      ) {
        propertyValue = this.hitSource[key][this.type.type];
      }

      if (propertyValue) {
        return propertyValue.join(',');
      }
      return '';
    },
    resultPrintout() {
      let propertyValue;
      let out = '';
      const that = this;
      const key = `P:${this.keyid}`;

      if (
        this.hitSource[key]
        && this.hitSource[key][this.type.type]
      ) {
        propertyValue = this.hitSource[key][this.type.type];
      }

      if (propertyValue) {
        propertyValue = propertyValue.sort();

        const outputs = {
          pill: () => {
            propertyValue.forEach((element) => {
              const forkey = `${that.strip(that.name)}--${that.strip(element)}`;
              out += `<label for="${forkey}" class="wssearch--hit-pill" >
                        ${that.highlightProp(that.sanitize(element))}
                      </label>`;
            });
          },
          link: () => {
            propertyValue.forEach((element) => {
              let linktitle = element.split(':');
              linktitle = linktitle.length <= 1 ? element : linktitle[1];
              // eslint-disable-next-line no-undef
              out += `<a href="${mw.config.values.wgScript}/${that.sanitize(element)}" >
                        ${that.highlightProp(that.sanitize(linktitle))}
                      </a>`;
            });
          },
          image: () => {
            propertyValue.forEach((element) => {
              out += `<img loading="lazy" src="${element.replace(/\s/gim, '_')}" >`;
            });
          },
          template: () => {
            out = '';
          },
          default: () => {
            if (that.hitSource[key].dat_raw) {
              that.hitSource[key].dat_raw.forEach((element) => {
                const [, year, month, day] = element.split('/');
                // eslint-disable-next-line no-undef
                const monthName = mw.message(that.monthNames[month - 1]).text();
                out += `<span>
                         ${day} ${monthName}, ${year}
                        </span>`;
              });
            } else {
              propertyValue.forEach((element) => {
                out += `<span >
                          ${that.highlightProp(that.sanitize(element))}
                        </span>`;
              });
            }
          },
        };

        (outputs[this.type.display] || outputs.default)();
      }

      return out;
    },
    propertyClass() {
      const strppedname = `wssearch--hit__property-${this.strip(this.name)}`;
      return `${strppedname}  ${strppedname}--${this.strip(this.resultPrintout)}`;
    },
    namespaceName() {
      return this.hitSource.subject.namespacename
        ? `${this.hitSource.subject.namespacename}:`
        : '';
    },
  },
  watch: {
    propChange() {
      this.computeResultProp();
    },
  },
  mounted() {
    this.computeResultProp();
  },
  methods: {
    apiResult(data) {
      if (data.parse) {
        this.outputHtml = data.parse.text['*'];
      }
    },
    sanitize(str) {
      const lt = /</g;
      const gt = />/g;
      const ap = /'/g;
      const ic = /"/g;
      const preTag = /{@@_HIGHLIGHT_@@/g;
      const postTag = /@@_HIGHLIGHT_@@}/g;
      return str
        .replace(lt, '&lt;')
        .replace(gt, '&gt;')
        .replace(ap, '&#39;')
        .replace(ic, '&#34;')
        .replace(preTag, '<b class="wssearch-term-highlight">')
        .replace(postTag, '</b>');
    },
    computeResultProp() {
      let params;
      if (this.type.display === 'template') {
        this.outputHtml = '<div class="wssearch-hit-prop-loading"><div class="wssearch-hit-prop-loading-bg"></div></div>';
        params = {
          action: 'parse',
          text: `{{${this.type.template}
                 |Page=${this.namespaceName}${this.hitSource.subject.title}
                 |Value=${this.justTheValue}
                 }}`,
          format: 'json',
          wrapoutputclass: 'wssearch--parsed-prop-output',
          disablelimitreport: true,
        };
        this.$store.dispatch('doApiCall', {
          actions:
            {
              params,
              component: this,
            },
        });
      } else {
        this.outputHtml = this.resultPrintout;
      }
    },
    strip(name) {
      return name.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
    highlightProp(prop) {
      let out = prop;
      const { term } = this.$store.state;
      const regex = new RegExp(term, 'gmi');

      if (term && this.type.highlight) {
        out = prop.replace(regex, `<b class="wssearch-term-highlight">${term}</b>`);
      }

      if (this.type.label && this.settings.layout !== 'table') {
        out = this.type.label + prop;
      }

      return out;
    },
  },
};
</script>

<style>
@-webkit-keyframes MOVE-BG {
  0% {
    background-position: -1.42857143em 0;
  }

  100% {
    background-position: 0 0;
  }
}

@keyframes MOVE-BG {
  0% {
    background-position: -1.42857143em 0;
  }

  100% {
    background-position: 0 0;
  }
}

.wssearch-hit-prop-loading {
  border-radius: 23px;
  height: 1em;
  display: inline-block;
  position: relative;
  overflow: hidden;
  min-width: 8em;
}

.wssearch-hit-prop-loading-bg {
  position: absolute;
  left: -46px;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;

  background: -webkit-linear-gradient(
    135deg,
    #fff 25%,
    transparent 25%,
    transparent 50%,
    #fff 50%,
    #fff 75%,
    transparent 75%,
    transparent
  );
  background: linear-gradient(
    135deg,
    #fff 25%,
    transparent 25%,
    transparent 50%,
    #fff 50%,
    #fff 75%,
    transparent 75%,
    transparent
  );
  background-color: #eaecf0;
  background-size: 1.42857143em 1.42857143em;
  -webkit-animation-name: MOVE-BG;
  -webkit-animation-duration: 0.6s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;

  animation-name: MOVE-BG;
  animation-duration: 0.6s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.wssearch--hit-pill {
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  background-color: white;
  padding: 0 0.5em 0 0.5em;
  display: inline-block;
  max-width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.92857143em;
  cursor: pointer;
  position: relative;
  white-space: nowrap;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: auto;
  max-width: 100%;
  height: 1.57142857em;
  margin: 0 4px 4px 0;
  border: var(--border-1);
  border-radius: 1.57142857em;
  line-height: 1.42857143em;
  vertical-align: middle;
}
</style>
