<template>
  <div class="wssearch--hit">
    <span class="wssearch--hit__link">
      <a
        :href="href"
        v-html="title"
      />
    </span>
    <img
      v-if="img"
      loading="lazy"
      class="wssearch--hit__img"
      :src="img"
    >
    <span
      class="wssearch--hit__body"
      v-html="exerpt"
    />
    <hit-prop
      v-for="(hitID, name) in config.hitSettings"
      :key="hitID.key"
      :keyid="hitID.key"
      :name="name"
      :type="hitID"
      :hit="hit"
    />
    <span
      v-if="namepaceName"
      class="wssearch--hit__namespace"
    >
      {{ namepaceName }}
    </span>
    <span class="wssearch--hit__pagetitle">
      {{ hit._source.subject.title }}
    </span>
    <span class="wssearch--hit__date">
      {{ date }}
    </span>
  </div>
</template>

<script>
import HitProp from './HitProp.vue';

export default {
  name: 'HitItem',
  components: {
    'hit-prop': HitProp,
  },
  props: {
    hit: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      monthNames: [
        'jan',
        'feb',
        'mar',
        'apr',
        'may',
        'jun',
        'jul',
        'aug',
        'sep',
        'oct',
        'nov',
        'dec',
      ],
      // eslint-disable-next-line no-undef
      config: mw.config.values.WSSearchFront.config,
      // eslint-disable-next-line no-undef
      configTitle: mw.config.values.WSSearchFront.config.settings.title,
      // eslint-disable-next-line no-undef
      scriptPath: mw.config.values.wgScript,
      // eslint-disable-next-line no-undef
      formatedNamespaces: mw.config.values.wgFormattedNamespaces,
    };
  },
  computed: {
    hitSource() {
      // eslint-disable-next-line no-underscore-dangle
      return this.hit._source;
    },
    namepaceName() {
      return this.formatedNamespaces
        ? this.formatedNamespaces[this.hitSource.subject.namespace]
        : '';
    },
    title() {
      let out = '';
      const key = this.configTitle ? `P:${this.configTitle.key}` : '';
      if (
        this.configTitle
        && this.configTitle.key
        && this.hitSource[key]
        && this.hitSource[key][this.configTitle.type]
      ) {
        out = this.highlightTitle(this.sanitize(this.hitSource[key][this.configTitle.type][0]));
      } else {
        out = this.highlightTitle(this.sanitize(this.hitSource.subject.title));
      }
      return out;
    },
    img() {
      let out = '';
      if (this.hitSource.file_path) {
        if (
          this.hitSource.attachment
          && this.hitSource.attachment.content_type === 'application/pdf'
        ) {
          const subjectTitle = this.hitSource.subject.title.replace(/\s/g, '_');
          out = (
            `/img_auth.php/thumb/${subjectTitle}/page1-300px-${subjectTitle}.jpg`
          );
        } else {
          out = this.hitSource.file_path;
        }
      }
      return out;
    },
    date() {
      let out = '';
      if (this.hitSource['P:29']) {
        const [, year, monthNumber, day] = this.hitSource['P:29'].dat_raw[0].split('/');
        // eslint-disable-next-line no-undef
        const month = mw.message(this.monthNames[monthNumber - 1]).text();
        out = `${day} ${month}, ${year}`;
      }
      return out;
    },
    exerpt() {
      if (this.hit.highlight) {
        let out = '';
        if (this.hit.highlight.text_raw) {
          this.hit.highlight.text_raw.forEach((element) => {
            out += `<div>
                        ${this.sanitize(element)}
                    </div>`;
          });
        } else if (this.hit.highlight.attachment && this.hit.highlight.attachment.content) {
          return this.sanitize(this.hit.highlight.attachment.content[0]);
        } else {
          Object.keys(this.hit.highlight).forEach((key) => {
            this.hit.highlight[key].forEach((element) => {
              out += `<div>
                        ${this.sanitize(element)}
                      </div>`;
            });
          });
          return out;
        }
      }
      return '';
    },
    href() {
      let out = '';
      if (this.hitSource.file_path) {
        if (this.$store.state.term) {
          out = `${this.scriptPath}/Pdf_viewer?pdf=${this.hitSource.subject.title}&snippet=${this.$store.state.term}`;
        } else {
          out = `${this.scriptPath}/Pdf_viewer?pdf=${this.hitSource.subject.title}`;
        }
        return out;
      }

      let urlString = '';
      const escapedTerm = this.$store.state.term
        .replace(/[|"&()*]/gim, '_%40_')
        .replace(/NOT/gm, '_%40_')
        .replace(/AND/gm, '_%40_')
        .replace(/OR/gm, '_%40_');
      if (this.configTitle && this.configTitle.urlstring) {
        urlString = `?${this.configTitle.urlstring}=${escapedTerm}`;
      }

      if (this.hitSource.subject.namespacename) {
        out = `${this.scriptPath}/${this.hitSource.subject.namespacename}:${this.hitSource.subject.title.replace(/\?/gim, '%3F')}${urlString}`;
      } else {
        out = `${this.scriptPath}/${this.hitSource.subject.title.replace(/\?/gim, '%3F')}${urlString}`;
      }
      return out;
    },
  },
  methods: {
    sanitize(str) {
      const lt = /</g;
      const gt = />/g;
      const ap = /'/g;
      const ic = /"/g;
      const preTag = /{@@_HIGHLIGHT_@@/g;
      const postTag = /@@_HIGHLIGHT_@@}/g;
      return str
        .replace(lt, '&lt;')
        .replace(gt, '&gt;')
        .replace(ap, '&#39;')
        .replace(ic, '&#34;')
        .replace(preTag, '<b class="wssearch-term-highlight">')
        .replace(postTag, '</b>');
    },
    highlightTitle(prop) {
      const term = this.$store.state.term.replace('*', '');
      if (term && this.configTitle && this.configTitle.highlight) {
        const regex = new RegExp(term, 'gmi');
        return prop.replace(regex, `<b class="wssearch-term-highlight">${term}</b>`);
      }
      return prop;
    },
  },
};
</script>

<style>
.wssearch--hit__link,
.wssearch--hit__body {
  display: block;
}
.wssearch--hit__link a {
  text-decoration: none;
  color: var(--base-color);
  font-size: 1.1em;
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
}
.wssearch--hit__link a:hover {
  color: var(--base-color-lighter);
  text-decoration: underline;
}
.wssearch--hits {
  display: grid;
  grid-gap: 1.5em;
  grid-auto-rows: max-content;
}
.wssearch--hit__link {
  font-weight: bold;
}
.wssearch--hit__link .wssearch-term-highlight {
  letter-spacing: 1px;
  text-shadow: 1px 0 0 currentColor, 1px 0 0 currentColor, 1px 0 0 currentColor;
}
.wssearch--hit__date,
.wssearch--hit__namespace,
.wssearch--hit__pagetitle {
  color: #1c8d1b;
}
</style>
