<template>
  <div class="wssearch--daterange">
    <div class="wssearch--filter-header">
      <label>{{ labelName }}</label>
    </div>
    {{ $i18n("wssearchfront-date-range-from") }}
    <div id="dateinputfrom" />
    {{ $i18n("wssearchfront-date-range-to") }}
    <div id="dateinputto" />
    <facet-checbox
      v-if="showCheckbox"
      :agg="agg"
      :index="0"
      :name="'Modification date'"
    />
  </div>
</template>

<script>
import FacetCheckbox from './FacetCheckbox.vue';

export default {
  name: 'FacetDateRange',
  components: {
    'facet-checbox': FacetCheckbox,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
  },
  computed: {
    labelName() {
      return this.label ? this.label : 'Date range';
    },
    showCheckbox() {
      if (this.$store.getters.rangeFrom && this.$store.getters.rangeTo) {
        return true;
      }
      return false;
    },
    agg() {
      return {
        key: 'customrange',
        from: this.$store.getters.rangeFrom,
        to: this.$store.getters.rangeTo,
        doc_count: 1,
      };
    },
  },
  mounted() {
    this.dateInputs();
  },
  methods: {
    dateInputs() {
      const { state } = this.$store;
      const that = this;
      const date = {};
      const dateInput = {};
      const dateInputs = ['from', 'to'];

      dateInputs.forEach((element) => {
        // eslint-disable-next-line no-undef
        dateInput[element] = new mw.widgets.DateInputWidget();

        if (state.realDates.customrange) {
          dateInput[element].setValue(state.realDates.customrange[element]);
        }

        dateInput[element].on('change', () => {
          date[element] = dateInput[element].getValue();
          dateInput.to.mustBeAfter = date.from;
          dateInput.from.mustBeBefore = date.to;

          that.$store.commit(
            `SET_RANGE_${element}`,
            that.createDates(date[element]) + 1,
          );

          let realdatesUpdated = state.realDates;
          if (state.rangeTo > 0) {
            realdatesUpdated = {
              customrange: {
                from: date.from,
                to: date.to,
              },
            };

            const selectedUpdated = state.selected;

            that.$store.commit('SET_REAL_DATES', realdatesUpdated);
            Object.keys(state.selected).forEach((_, i) => {
              if (state.selected && state.selected[i] && state.selected[i].value === 'customrange') {
                let te = '';
                if (element === 'from') {
                  te = 'gte';
                } else {
                  te = 'lte';
                }

                selectedUpdated[i].range[te] = Number(`${state[`range${element.charAt(0).toUpperCase()}${element.slice(1)}`]}.0000000`);
                that.$store.commit('SET_SELECTED', selectedUpdated);
              }
            });
          }
        });

        document
          .querySelector(`#dateinput${element}`)
          .appendChild(dateInput[element].$element[0]);
      });
    },
    createDates(date) {
      // 2451544 = 2000- 1- 1
      const timestamp = 2451544;

      function parseDate(str) {
        const pieces = [];
        str.split('-').forEach(piece => (piece.length === 1 ? pieces.push(`0${piece}`) : pieces.push(piece)));
        return new Date(pieces.join('-'));
      }

      function dateDiff(first, second) {
        return Math.round((second - first) / (1000 * 60 * 60 * 24));
      }

      return dateDiff(parseDate('2000-01-01'), parseDate(date)) + timestamp;
    },
  },
};
</script>

<style>
.wssearch--daterange .wssearch--checkbox-count,
.wssearch--daterange .wssearch--checkbox-title {
  display: none;
}
.wssearch--daterange .wssearch--checkbox {
  display: block;
}
.wssearch--daterange .wssearch--checkbox-input {
  position: relative;
  display: block;
}
.wssearch--daterange .wssearch--checkbox-input-icon {
  width: 100%;
}
</style>
