<template>
  <label
    class="wssearch--selected-filter"
    :class="propertyClass"
    :for="forKey"
  >
    <bdi>
      {{ title }}
    </bdi>
  </label>
</template>

<script>
export default {
  name: 'PillItem',
  props: {
    activefilter: {
      type: Object,
      default() {
        return {};
      },
    },
    label: {
      type: String,
      default: '',
    },
  },
  computed: {
    propertyClass() {
      const stripped = `wssearch--selected-filter__${this.strip(this.activefilter.key)}`;
      return `${stripped} ${stripped}--${this.strip(this.activefilter.value)}`;
    },
    forKey() {
      return `${this.strip(this.activefilter.key)}--${this.strip(this.activefilter.value)}`;
    },
    title() {
      return this.label ? this.label : this.activefilter.value;
    },
  },
  methods: {
    strip(string) {
      return string.toLowerCase().replace(/[^a-z0-9]/gim, '_');
    },
  },
};
</script>

